export default {
	data() {
		return {
			wishList:[],
			radio:0,
			step:2,
			form:{
				year:'',
				month:'',
				day:'',
				hour:''
			},
			sex:['男','女'],
			sexIndex:0
		}
	},
	onLoad() {
		uni.getStorage({
		    key: 'wishList',
		    success: (res)=>{
				console.log(res.data)
		        this.wishList=res.data||[]
		    }
		});
	},
	methods: {
		//选择性别
		selSex(index){
			this.sexIndex=index
			console.log(index)
		},
		//选择日历
		selRadio(index){
			console.log(index)
			if(this.radio===index){
				return
			}
			this.radio=index
		},
		submit(){
			console.log(this.form)
			this.step++
		}
	}
}
